<template>
    <div class="item" @click="release($event.currentTarget)" :data-before-content="this.sideInfo">
      
        <div class="texts">

            <div class="title">
                {{ this.title }}
            </div>
        
            <div class="description">
                {{ this.description }}
            </div>

        </div>

    </div>
</template>

<script>

export default {

    name: 'Item',

    props: {
        title: String,
        description: String,
        sideInfo: String
    },

    methods: {
        release(target) {
            this.$emit('activated', 'jsonstream.herokuapp.com' + (target.children[0].children[0].innerHTML).trim());
        }
    }
}

</script>

<style lang="scss">

    $floater_offset: 101%;

    .item {
        color: #ffe6e6;
        padding: 1em;
        border-radius: 5px;
        width: 300px;
        cursor: pointer;
        position: relative;
        box-shadow: 0 10px 20px rgba(0,0,0,.19), 0 6px 6px rgba(0,0,0,.1);
        transition: left 0.3s, box-shadow 0.3s, color 0.3s;
        text-align: center;
        left: 0;
        z-index: 100;
        overflow-x: hidden;
        background-color: #ff6666;
        margin-top: -3px;

        &::before {
            content: attr(data-before-content);
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(-($floater_offset), -50%);
            transition: transform 0.7s;
            font-weight: bold;
            font-size: 110%;
            color: #ffb3b3;
        }

        &:hover:before {
            opacity: 1;
            transform: translate($floater_offset/5, -50%);
        }

        &:hover {
            left: 2em;
            box-shadow: none;
            color: white;
        }

        .title {
            text-align: center;
        }

        .description {
            margin-top: .5rem;
            font-size: .70rem;
        }
    }

    @media only screen and (max-width: 736px) {
        .item {
            width: 100%;
            height: 400px;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: none;
            padding-left: 0;
            padding-right: 0;
            border-radius: 0;

            &:hover {
                left: 0;
            }

            .texts {
                background-color: #ff6666;
                padding: 3em;
            }
        }
    }

</style>
