<template>
    <div class="wrapper">

        <div class="divider"></div>

        <div class="row noflex fullheight">
            <div class="shape triangle"></div>
            <div class="shape triangle"></div>
            <div class="cover">
                {{ cover }}
            </div>
        </div>

        <div class="row">
            <div class="inner">

                <div class="choose">
                    <span @click.prevent="changeMethod('GET')" :class="{selected: getActive}">GET</span>
                    <span @click.prevent="changeMethod('POST')" :class="{selected: postActive}">POST</span>
                </div>

                <div class="container" v-for="(item, idx) in this.feed" v-bind:key="idx">
                    <Item :title="item.title" :description="item.description" :sideInfo="item.sideInfo" @activated="handle" :style="{zIndex:(feed.length-idx)}" v-if="item.sideInfo === method"></Item>
                </div>
            </div>

            <div class="inner info">
                <div class="textfield">
                    <div class="display">{{ this.text }}</div>
                </div>
            </div>
        </div>
  
    </div>
</template>

<script>

    import Item from '@/components/Item.vue'
    import feed from '@/feed.js'

    export default {
    
        name: 'home',
       
        components: {
            Item
        },

        data() {
            return {
                feed: feed.items,
                text: 'API',
                cover: process.env.VUE_APP_TITLE,
                method: 'GET'
            }
        },

        computed: {

            getActive() { 
                return this.method === "GET"
            },

            postActive() { 
                return this.method === "POST"
            },
        },

        methods: {
            handle(value) {
                this.text = value;
            },

            changeMethod(method) {
                this.method = method;
            }
        },

        mounted() {

            let origins = [ document.getElementsByClassName('triangle')[0].offsetTop, 
                            document.getElementsByClassName('triangle')[1].offsetTop ];

            window.onscroll = () => {
                
                if(document.getElementsByClassName("divider")[0].offsetHeight > 0) {
                    window.onscroll = "";
                }
            
                document.getElementsByClassName('triangle')[0].style.top = origins[0] + window.scrollY/3 + 'px';
                document.getElementsByClassName('triangle')[0].style.transform = 'rotate(' + (-20 + (window.scrollY/50)) + 'deg)';
                document.getElementsByClassName('triangle')[1].style.top = origins[1] + window.scrollY/10 + 'px';
            }
        }
    }

</script>

<style lang="scss">
  
    .home {
        width:100%;
        height: 100%;
    }

    html {
        width: 100%;
        height: 100%;
        margin: 0;
        font-size: 20px;
    }

    body {
        background-color: #ff6666;
        width: 100%;
        height: 100%;
        margin: 0;
        overflow-x: hidden;
    }

    .wrapper {

        font-family: 'Open Sans', sans-serif;
        text-align: center;
        height: 100%;

        .row {
            border: 3px solid #ffe6e6;

            &:not(.noflex) {
                display: flex;
            }

            &.fullheight {
                height: calc(100% - 6px);
            }

            .shape {
                transition: top 1s, transform 1s;

                &.triangle {
                    position: absolute;
                    width: 0;
                    height: 0;
                    border-bottom: 500px solid #ff8080;
                    border-right: 500px solid transparent;
                    transform: rotate(-20deg);
                    z-index: -1;
                    top: -100px;

                    &:not(:first-child) {
                        right: 0;
                        bottom: -100px;
                        top: auto;
                        transform: rotate(120deg);
                    }
                }
            }
        }

        .cover {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            color: #ffe6e6;
            font-size: 3rem;
        }

        .inner {
            flex: 50%;
            color: #ffe6e6;
            font-weight: normal;

            .choose {
                margin-bottom: 2rem;
                font-size: 70%;
                user-select: none;

                span {
                    background-color: #ff6666;
                    transition: background-color .3s;
                    user-select: none;

                    &.selected {
                        background-color: #ff4d4d;
                    }

                    &:first-child {
                        margin-right: .5rem;
                    }

                    &:hover {
                        background-color: #ff4d4d;
                    }

                    padding: .5rem .8rem;
                    border: 2px solid #ffe6e6;
                    border-radius: 5px;
                    cursor: pointer;
                }
            }

            .textfield {
                position: relative;
                top: 50%;
                transform: translateY(-50%);
                color: #ffe6e6;
                font-weight: bold;
                font-size: 1.1rem;
                animation: float 10s ease-in-out infinite;
                background-color: #ff8080;
                padding: 2em;
                border: 5px solid #ffe6e6;
                border-radius: 5px;
            }
        }

        & .inner:first-child {
            word-wrap: break-word;
            flex-direction: column;
            text-align: left;
            padding: 1em;
            border-right: 3px solid #ffe6e6;
            background-color: #ff6666;
        }

        & .inner.info {
            background-color: #ff4d4d;
            padding: 1rem;
            flex: 25%;
        }
    }

    @keyframes float {
        0% {
            transform: translatey(0px) rotate(5deg);
            box-shadow: 0 5px 15px 0px rgba(0,0,0,0.4);
        }
        50% {
            box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
            transform: translatey(-20px) rotate(5deg);
        }
        100% {
            box-shadow: 0 5px 15px 0px rgba(0,0,0,0.4);
            transform: translatey(0px) rotate(5deg);
        }
    }

    @media only screen and (max-width: 736px) {

        .wrapper {


            .divider {
                width: 2px;
                height: 3100px;
                background-color: #ffe6e6;
                position: absolute;
                left: 50%;
                top: calc(100% + 200px);
            }

            .row {
                &:not(.noflex) {
                    display: block;
                }
                .shape.triangle {
                    display: none;
                }
            }

            .inner {
                overflow-x: hidden;
                display: flex;
                justify-content: center;
                align-items: center;
                border: none;

                .choose {
                    display: none;
                }
                
                &:first-child {
                    padding: 0;
                }

                .container {
                    width: 100%;
                }

                &.info {
                    padding: 0;
                }

                > span {
                    font-size: 1.5rem;
                }

                .textfield {
                    animation: none;
                    transform: none;
                    width: 100%;
                    word-wrap: break-word;
                    max-width: 100%;
                    border: none;
                    display: none;
                    border-top: 3px solid #ffe6e6;

                    .display {
                        padding-left: 1em;
                        padding-right: 1em;
                    }
                }

                &:first-child {
                    border: none;
                }
            }
        }
    }

</style>